<template>
    <div class="container mt-5">
        <div class="card">
            <div class="card-body">
                <h2 class="text-center mb-4">공지사항 상세</h2>

                <div v-if="renderNoticeDetail">
                    <div class="notice-header">
                        <h3 class="card-title">{{ renderNoticeDetail.title }}</h3>
                    </div>

                    <div class="notice-content">
                        <p class="card-text">{{ renderNoticeDetail.contents }}</p>
                    </div>

                    <div class="notice-image">
                        <img :src="renderNoticeDetail.img" class="img-fluid" alt="공지사항 이미지">
                    </div>
                </div>
                <div v-else>
                    <p>공지사항을 찾을 수 없습니다.</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { noticeDetail } from '@/testData';
export default {
    name: "NoticeDetailVue",
    data() {
        return {
            noticeDetail: noticeDetail,
            noticeID: this.$route.params.noticeID,
            renderNoticeDetail: {}
        }
    },
    created() {
        this.noticeID = this.$route.params.noticeID;
        this.renderNoticeDetail = this.noticeDetail.find(item => item.id == this.noticeID);
    }
}
</script>

