<template>
    <div class="container mt-5">
        <h2 class="text-center mb-4">장바구니</h2>
        <div v-if="cartItems.length">
            <div class="row mb-3" v-for="(item, index) in cartItems" :key="index">
                <div class="col-md-12">
                    <div class="card">
                        <div class="row g-0">
                            <div class="col-md-4">
                                <img :src="item.img" class="img-fluid rounded-start" alt="상품 이미지">
                            </div>
                            <div class="col-md-8">
                                <div class="card-body">
                                    <h5 class="card-title">{{ item.title }}</h5>
                                    <p class="card-text">수량:</p>
                                    <div class="input-group mb-3" style="max-width: 200px;">
                                        <button class="btn btn-outline-secondary" type="button" @click="decreaseQuantity(index)">-</button>
                                        <input type="text" class="form-control text-center" v-model.number="item.quantity">
                                        <button class="btn btn-outline-secondary" type="button" @click="increaseQuantity(index)">+</button>
                                    </div>
                                    <button class="btn btn-danger" @click="removeItem(index)">삭제</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <button class="btn btn-primary" @click="sellProd()">구입하기</button>
        </div>
        <div v-else>
            <p class="text-center">장바구니에 상품이 없습니다.</p>
        </div>
    </div>
</template>

<script>
export default {
    name: "STR0001Vue",
    data() {
        return {
            cartItems: []
        }
    },
    created() {
        this.cartItems = this.$store.state.cartItems
    },

    methods: {
        increaseQuantity(index) {
            this.$store.commit('increase', index);
        },
        decreaseQuantity(index) {
            if (this.cartItems[index].quantity > 1) {
                this.$store.commit('decrease', index);
            }
        },
        removeItem(index) {
            this.$store.commit('delete', index);
        },
        sellProd() {
            alert("구입을 완료하였습니다");
            this.$store.commit('reset');
            this.$router.go(-1);
        }
    }
}
</script>

