import { createWebHistory, createRouter } from "vue-router";
import HOM0001 from "./views/HOM/HOM0001.vue";
import HOM0002 from "./views/HOM/HOM0002.vue";
import STR0001 from "./views/STR/STR0001.vue"
import NOT0001 from "./views/NOT/NOT0001.vue";
import NOT0002 from "./views/NOT/NOT0002.vue";

const routes = [
    {
        path: "/",
        component: HOM0001,
        name: "HOM0001"
    },
    {
        path: "/HOM0001/:productID",
        component: HOM0002,
        name: "HOM0002"
    },
    {
        path: "/STR0001",
        component: STR0001,
        name: "STR0001"
    },
    {
        path: "/NOT0001",
        component: NOT0001,
        name: "NOT0001"
    },
    {
        path: "/NOT0001/:noticeID",
        component: NOT0002,
        name: "NOT0002"
    }
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL), // 기본 경로 설정
    routes,
  });
  
  export default router; 