<template>
    <div class="container mt-5">
        <h2 class="text-center mb-4">{{ renderproductDetail.title }}</h2>
        <div class="row">
            <div class="col-md-6">
                <img :src="require(`../../assets/${renderproductDetail.img}`)" class="img-fluid" alt="상품 이미지">
            </div>
            <div class="col-md-6">
                <h3>{{ renderproductDetail.title }}</h3>
                <p>{{ renderproductDetail.description }}</p>
                <button href="#" class="btn btn-success" @click="StoreAdd()">장바구니 담기</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "HomeDetailVue",
    data() {
        return {
            productID: this.$route.params.productID,
            renderproductDetail: {},
            productDetail: [
                { id: 1, title: '상품 1', description: '상품 설명 1', img: require('@/assets/testimg1.jpg') },
                { id: 2, title: '상품 2', description: '상품 설명 2', img: require('@/assets/testimg2.jpg') },
                { id: 3, title: '상품 3', description: '상품 설명 3', img: require('@/assets/testimg3.jpg') },
            ],
            productDetailCP : [],

            productDetail2: [
                { id: 1, title: '상품 1', description: '상품 설명 1', img: 'testimg1.jpg' },
                { id: 2, title: '상품 2', description: '상품 설명 2', img: 'testimg2.jpg' },
                { id: 3, title: '상품 3', description: '상품 설명 3', img: 'testimg3.jpg' },
            ]
        }
    },

    created() {
        this.productID = this.$route.params.productID;
        this.renderproductDetail = this.productDetail2.find(item => item.id == this.productID);
        this.productDetailCP = this.productDetail.find(item => item.id == this.productID);
    },

    methods: {
        StoreAdd() {
            this.$store.commit('insert', this.productDetailCP);
            this.$router.push('/STR0001');
        }
    }
}
</script>

